html {
    overflow-y: scroll;
    overflow-x: scroll;
}

body,
div,
ol,
ul,
h1,
h2,
h3,
h4,
h5,
h6,
p,
th,
td,
dl,
dd,
form,
fieldset,
legend,
input,
textarea,
select {
    margin: 0;
    padding: 0;
}


a {
    color: #0066FF ;
    text-decoration: none;
}

a:hover {
    color: #cd0200;
    text-decoration: underline;
}
a:visited {
    color: #0066FF;
    text-decoration: none;
}

em {
    font-style: normal;
}


img {
    border: 0;
    vertical-align: middle;
}

table {
    border-collapse: collapse;
    border-spacing: 0;
}

p {
    word-wrap: break-word;
}




